export default {
	data() {
		return {
			todoList: [],
			nodata: 'http://data.huwei-soft.cn/schooltest/static/nodata.png'
		}
	},
	methods: {
		gotoNext(item) {
			uni.navigateTo({
				url: 'dangerFileDetail/dangerFileDetail?No=' + item.todoid + "&CLQK=" + item.CLQK + "&YSCD=" + item.YSCD + '&AQZK=' + item.AQZK + "&YHXH=" + item.YHXH
			})
		},
		getDate() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Get_MyCur_Problems',
				data: {
				
					RYXH: self.$store.state.userInfo.RYXH
				},
			}).then(data => {
				var [error,res] = data;
				
				if (res.data == '暂无数据') {
				} else {
					let data = JSON.parse(res.data);
					self.todoList = data;
					console.log(self.todoList);
				}
			})
		},
	},
	mounted() {
		this.getDate()
	}
}
